.statistics {
  background-color: #fff;
  text-align: center;
  padding-top: 20px;
}
.title {
  padding-bottom: 10px;
  text-transform: uppercase;
  font-weight: 500;
}
.stat_list {
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  list-style-type: none;
  align-items: stretch;
}
.item {
  width: 100%;
  padding-bottom: 25px;
  padding-top: 25px;
}
